import React, {useState, useEffect} from 'react';
import fire from '../config/fire';

const Login = (props) => {
    
    

    const [ usr, setUser ] = useState('')
    const [ loggedIn, setLoggedIn ] = useState(false)
    const [ email, setEmail ] = useState('')
    const [ emailError, setEmailError ] = useState('')
    const [ password, setPassword ] = useState('')
    const [ password2, setPassword2 ] = useState('')
    const [ passwordError, setPasswordError ] = useState('')
    const [ hasAccount, setHasAccount ] = useState(true)
    const [ isHover, setHover ] = useState(false)

    function clearInput(){
        setEmail('')
        setPassword('')
        setPassword2('')
    }

    function clearError(){
        setEmailError('')
        setPasswordError('')
    }

    function handleLogin(event){
        clearError()
        event.preventDefault();
        fire
            .auth()
            .signInWithEmailAndPassword(email, password)
            .catch(err => {
                switch(err.code){
                    case "auth/invalid-email":
                    case "auth/user-disabled":
                    case "auth/user-not-found":
                        setEmailError(err.message);
                        break;
                    case "auth/wrong-password":
                        setPasswordError(err.message);
                        break;

                }
            });

        if( !emailError && !passwordError){
            setLoggedIn(true)
            clearInput()
        } else
            event.preventDefault();
        
        
    }

    function handleSignup(event){
        clearError()
        fire
            .auth()
            .createUserWithEmailAndPassword(email, password)
            .catch(err => {
                switch(err.code){
                    case "auth/email-already-in-use":

                    case "auth/invalied-email":    
                        setEmailError(err.message);
                        break;    
                    case "auth/weak-password":
                        setPasswordError(err.message);
                        break;
                }
            });
        if( !emailError|| !passwordError){
            setHasAccount(true)
        } else
            event.preventDefault();
        

    }

    function handleLogout(even){
        fire.auth().signOut()
        setLoggedIn(false)
    }

    function handleSignup_Signin(){
        setHasAccount(!hasAccount);
    }

    function authListener(){
        fire.auth().onAuthStateChanged(user =>{
            if(user){
                setUser(user.email)
                
            }
            else{
                setUser('')
            }
        })
    }

    useEffect(()=>{
        authListener()
    }, [])


    return (
        <div>
            
            { loggedIn===false && <form onSubmit={ hasAccount? handleLogin : handleSignup }>
            
                <input name="email"
                    value={email}
                    onChange={event => setEmail(event.target.value)}
                    
                    style={emailError===''?{
                        padding: "10px",
                        background:"white",
                        border:'0px',
                        width:"150px",
                        backgroundColor:'white',
                        borderColor:'white'
                    }:{
                        padding: "10px",
                        background:"white",
                        border:'0px',
                        width:"150px",
                        backgroundColor:'#FA5858',
                        borderColor:'white'
                    }}

                    placeholder="Email">
                </input>
                
                <div style={{fontSize:'10px', color:'red'}} >{emailError}</div>

                
                <input name="password"
                    value={password}
                    type="password"
                    onChange={event => setPassword(event.target.value)}
                    style={passwordError===''?{
                        padding: "10px",
                        background:"white",
                        border:'0px',
                        width:"150px",
                        backgroundColor:'white',
                        borderColor:'white'
                    }:{
                        padding: "10px",
                        background:"white",
                        border:'0px',
                        width:"150px",
                        backgroundColor:'#FA5858',
                        borderColor:'white'
                    }}
                    placeholder="Password">
                
                </input>
                <div style={{fontSize:'10px', color:'red'}} >{passwordError}</div>
                { !hasAccount &&  <input name="password2"
                    value={password2}
                    type="password"
                    onChange={event => setPassword2(event.target.value)}
                    style={password===password2?{
                        padding: "10px",
                        background:"white",
                        border:'0px',
                        width:"150px",
                        backgroundColor:'white',
                        borderColor:'white'
                    }:{
                        padding: "10px",
                        background:"white",
                        border:'0px',
                        width:"150px",
                        backgroundColor:'#FA5858',
                        borderColor:'white'
                    }}
                    placeholder="Confirm Password">
                
                </input>}
                <div style={{fontSize:'10px', color:'red'}} >{ !hasAccount&&password!==password2? 'Password does not match' : null}</div>
                
                
                <input type="submit" value={hasAccount?'Login':'Signup'} style={{
                    // gradient from #FE6B8B to #FF8E53
                    width:"150px",
                    margin:"10px",
                    background: isHover? 'linear-gradient(45deg, #FF8E53 30%, #FE6B8B 90%)' : 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
                    border: 0,
                    borderRadius: 3,
                    boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
                    color: 'white',
                    height: 48,
                    padding: '0 30px',
                    cursor: 'cursor'
                    }}
                    onMouseEnter={event => setHover(true)}
                    onMouseLeave={event => setHover(false)}/>
                <div style={{fontSize:'12px', color:'white'}}
                    onClick={handleSignup_Signin}>{hasAccount? 'Signup' : 'Login'}</div>
                
                
            </form> }

                { loggedIn===true && 
                <button onClick={handleLogout}
                    style={{
                    // gradient from #FE6B8B to #FF8E53
                        width:"auto",
                        margin:"10px",
                        background: isHover? 'linear-gradient(45deg, #FF8E53 30%, #FE6B8B 90%)' : 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
                        border: 0,
                        borderRadius: 3,
                        boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
                        color: 'white',
                        height: 48,
                        padding: '0 30px',
                        cursor: 'cursor'
                    }}
                    onMouseEnter={event => setHover(true)}
                    onMouseLeave={event => setHover(false)}>LOG OUT {usr}</button> }
            
            
        </div>
    )

}

export default Login;