import React, {useState} from 'react';
import Button from '@material-ui/core/Button';

const StyledButton = (props) => {
    const [mouse, setMouse] = useState(false)
    function changeBackground(e) {
      e.target.style.background = 'linear-gradient(45deg, #FF8E53 30%, #FE6B8B 90%)';
    }
    function buttonColor(e){
      e.target.style.background = 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)';
    }

    return(<button
        style={{
          // gradient from #FE6B8B to #FF8E53
          width:"150px",
          margin:"10px",
          background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
          border: 0,
          borderRadius: 3,
          boxShadow: '0 3px 5px 2px rgba(255, 105, 135, .3)',
          color: 'white',
          height: 48,
          padding: '0 30px',
          cursor: 'cursor'
        }}
        type="button"
    
        onMouseEnter={changeBackground}
        onMouseLeave={buttonColor}
        onClick={(e) => { 
          if(typeof props.func == 'function')
            props.func()  
          if(props.link!=='')
            window.location.href=props.link;
          e.preventDefault();
          }
          
        }>{props.children}</button>)
    


}


export default StyledButton;